export default{
	name: 'modal',
	props: {
		title: {
			type: String,
			default: '提示'
		},
		text: {
			type: String,
			default: ''
		},
		noCancel: {
			type: Boolean,
			default: false
		},
		cancelText: {
			type: String,
			default: '取消'
		},
		cancelStyle: {
			type: [String, Object]
		},
		confirmText: {
			type: String,
			default: '确定'
		},
		confirmStyle: {
			type: [String, Object]
		},
		textCenter:{
			type:Boolean,
			default:false
		},
		prevent: {
			type: Boolean,
			default: true
		},
		value: {
			type: Boolean,
			default: false
		}
	},
	data(){
		return{
			showValue: this.value
		}
	},
	watch: {
		value(n, o){
			this.showValue = n
		},
		showValue(n, o){
			this.$emit('input', n)
		}
	},
	methods: {
		confirm(){
			this.showValue = false
			let msg = {from: 'confirm', confirm: true}
			this.$emit('confirm', msg)
			this.$emit('event', msg)
		},
		cancel(type){
			if(this.prevent && type === 2){
				return;
			}
			this.showValue = false
			let msg = {from: type === 1 ? 'cancel' : 'mask'}
			type === 1 ? msg.cancel = true : msg.mask = true
			this.$emit('cancel', msg)
			this.$emit('event', msg)
		}
	}
}
